import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Gif extends Component {
	constructor(props) {
		super(props);

		this.state = {
			authorDetails: [],
			isClicked: false,
			error: null
		}
	}

	handleArticleClick = () => {
		this.setState(() => ({ isClicked: true }))
	}
	
	componentDidMount() {
	  this.fetchAuthorDetails();
	}

	fetchAuthorDetails() {
	  fetch(`http://localhost:3000/api/v1/getUser/${this.props.authorId}`, {
	  	headers: {
	  		'Content-Type': 'application/json',
	  		'Authorization': "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VySWQiOjExLCJpYXQiOjE1NzY1MzUxMjMsImV4cCI6MTU3NzEzOTkyM30.Xs39XzkS4CEMPPil-ZKYst0aV7FLSAVy5l4mdSu0HZk"
	  	}
	  })
	  .then(response => response.json())
	  .then(data => {
		  // console.log(data)
		  if (data.status === 'success') {
		    this.setState({
			    authorDetails: data.data,
			  })
		  } 
		  else {
				this.setState({ error: data.error.message })	    		      
	    }
	  })
	  .catch(error => console.log(error));		
	}

	render() {
		const { gifid, title, imageurl, createdOn } = this.props;
		const [{...obj}] = this.state.authorDetails;
		const { firstname, lastname } = obj;
		const name = `${firstname} ${lastname}`;

		return (
			<Link to={`/FullGifPage/${gifid}`}>
				{
					<div className="jumbotron text-center" key={gifid}>
						<h1 className="display-4">{title}</h1>
						<img src={imageurl} width="300" height="300" alt={title} />
						<p>by {name}</p>
						<hr className="my-4"></hr>
						<p>{createdOn}</p>
					</div>
				}
			</Link>
		);
	}
}
