import React, { Component } from 'react';
import Article from './Article';
import Gif from './Gif';


class Dashboard extends Component {
	constructor(props) {
		super(props);

		this.state = {
	    isLoading: true,
	    feeds: [],
	    error: null
		}
	}

	// handleArticleClick = () => {
	// 	console.log('working')
	// 	this.props.history.push('/FullArticlePage')
	// }

	componentDidMount() {
	  this.fetchUsers();
	}

	fetchUsers() {
  fetch(`http://localhost:3000/api/v1/feed`, {
  	headers: {
  		'Content-Type': 'application/json',
  		'Authorization': "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VySWQiOjExLCJpYXQiOjE1NzY1MzUxMjMsImV4cCI6MTU3NzEzOTkyM30.Xs39XzkS4CEMPPil-ZKYst0aV7FLSAVy5l4mdSu0HZk"
  	}
  })
    .then(response => response.json())
    .then(data => {
	    	// console.log(data)
	    	if (data.status === 'success') {
	    		this.setState({
		        feeds: data.data,
		        isLoading: false,
		      })
	    	} else {
					this.setState({ error: data.error.message, isLoading: false })	    	}	      
    		}
    )
    .catch(error => this.setState({ error, isLoading: false }));
}

	render() {
		const { isLoading, feeds, error } = this.state;
		return (
			<div>
				<h2>Dashboard</h2>
      	{error ? <p>{error}</p> : null}
      	{
      		!isLoading 
      		? 
					(feeds.map(feed => {
						const { articleId, gifid, authorId, title, article, imageurl, createdOn } = feed;
						return (
							<div key={Math.random() * 3}>
								{typeof articleId === 'number' 
								? 
								<Article articleId={articleId} authorId={authorId} title={title} article={article} createdOn={createdOn.toString()} /> 
								: 
								<Gif gifid={gifid} authorId={authorId} title={title} imageurl={imageurl} createdOn={createdOn.toString()} />}
							</div>							
						)
					})) 
					: 
					(
						<div className="d-flex justify-content-center">
							<div className="spinner-border" role="status">
								<span className="sr-only">Loading...</span>
							</div>
						</div>
					)
				}
			</div>
		);
	}
}


export default Dashboard;