import React,{ Component } from 'react';

export default class FullArticlePage extends Component {
	constructor(props) {
		super(props);

		this.state = {
			article: null,
			error: null
		}
	}

// this.props.match.params.id

 	componentDidMount() {
	  this.fetchArticleDetails()
	}

	fetchArticleDetails = () => {
	  fetch(`http://localhost:3000/api/v1/articles/${this.props.match.params.id}`, {
	  	headers: {
	  		'Content-Type': 'application/json',
	  		'Authorization': "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VySWQiOjExLCJpYXQiOjE1NzY1MzUxMjMsImV4cCI6MTU3NzEzOTkyM30.Xs39XzkS4CEMPPil-ZKYst0aV7FLSAVy5l4mdSu0HZk"
	  	}
	  })
	  .then(response => response.json())
	  .then(data => {
		  // console.log(data)
		  if (data.status === 'success') {
		    this.setState({
			    article: data.data,
			  })
		  } 
		  else {
				this.setState({ error: data.error.message })	    		      
	    }
	  })
	  .catch(error => this.setState({ error }));		
	}
	
	show =() => console.log(this.state) 

	render() {
		const { article, title, createdOn } = {...this.state.article};
		// if(error){ return <p>{error}</p> } else { return null}

		return (
			<div className="jumbotron text-center" onClick={this.show}>
				<h1 className="display-4">{title}</h1>
				<p className="lead">{article}</p>
				<p>{createdOn}</p>
			</div>
		);
	}
}
